import { _decorator, Component, Node, Slider, Button, Label, labelAssembler, convertUtils, math } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('BetModal')
export class BetModal extends Component {

    @property(Node)
    private self : Node = null;
    @property(Slider)
    private betSlider : Slider = null;
    @property(Button)
    private betButtonList : Array<Button> = new Array<Button>();
    @property(Button)
    private decideButton : Button = null;
    @property(Label)
    private valueLabel : Label = null;
    @property(Button)
    private closeButton : Button = null;

    private coins : number = 20000;
    private betValue : number = 0;
    private criteriaPoint : number = 0;

    start() {
        this.decideButton.node.on(Button.EventType.CLICK, function(){this.self.active = false;}, this);
        this.closeButton.node.on(Button.EventType.CLICK, function(){this.self.active = false;}, this);
        this.betButtonList[0].node.on(Button.EventType.CLICK, function(){this.betValue += 10;}, this);
        this.betButtonList[1].node.on(Button.EventType.CLICK, function(){this.betValue += 100;}, this);
        this.betButtonList[2].node.on(Button.EventType.CLICK, function(){this.betValue += 1000;}, this);
        this.betButtonList[3].node.on(Button.EventType.CLICK, function(){this.betValue = this.coins;}, this);
        this.criteriaPoint = this.coins / 10;
    }

    update(deltaTime: number) {
        this.BetSlider();
    }

    private BetSlider(){

        this.betValue = Math.round(this.coins * this.betSlider.progress);
        this.valueLabel.string = this.betValue.toString();
    }
}

